<script>
import functions from "../../util/functions.js";
import constants from "../../util/constants";

export default {
  props: {
    add_mode: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    ...functions, 
    verifierChargement() {
      this.nbr_charges += 1;
      if (this.nbr_charges == this.nbr_total_chargements) {
        this.is_loading_articles = false;
      }
    },
    async loadArticle() {
      let url =
        this.constants.api_url +
        "/articles/get.php?id_article=" +
        encodeURIComponent(this.$route.params.id_article);
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS" && data.article) {
        this.article = {
            ...data.article,
            // est_disponible :   data.article.est_disponible == 1 ? true : false,
            // est_en_promotion :   data.article.est_en_promotion == 1 ? true : false,
            // est_nouveau :   data.article.est_nouveau == 1 ? true : false, 
        };
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    async loadCollections() {
      let url =
        this.constants.api_url +
        "/collections/get.php" ;
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS" && data.collections) {
        this.listCollections = data.collections;
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    onImageAdded(image) {
      this.article.images = [...this.article.images, image];
    },
    onImageUpdated(image) {
      let index = this.article.images.findIndex(
        (img) => img.id_image == image.id_image
      );
      if (index >= 0) {
        this.article.images[index] = image;
      }
    },
    onImageDeleted(image) {
      this.article.images = this.article.images.filter(
        (img) => img.id_image != image.id_image
      );
    },
    verifierArticle(article) {
      let message = "";
      if (article.nom == undefined || article.nom == "") {
        message = "Veuillez entrer un nom correcte svp";
      } else if (article.collection_id == undefined || article.collection_id == -1) {
        message = "Veuillez sélectionner une collection svp";
      } else if (article.prix_unitaire == undefined || !parseFloat(article.prix_unitaire) || parseFloat(article.prix_unitaire) <= 0) {
        message = "Veuillez entrer un prix unitaire correcte svp";
      } else if (article.prix_barre == undefined || isNaN(parseFloat(article.prix_barre)) || (article.est_en_promotion && parseFloat(article.prix_barre) <= 0)) {
        message = "Veuillez entrer un prix promotionnel correcte svp";
      }  else if (article.description == undefined || article.description == "") {
        message = "Veuillez entrer une description correcte svp";
      } 
      if (message != "") {
        this.$swal({
          type: "warning",
          title: "Les données fournies sont incorrcetes",
          text: message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      return {
        status: message === "",
        message,
      };
    },
    async doAddArticle() {
      if (!this.verifierArticle(this.article).status) return;

      this.loading_message = "Enregistrement du article  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/articles/add.php";
      let response = null;

      try {
         let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          credentials: "include",
          headers,
          body: "nom=" +
            encodeURIComponent(this.article.nom) +
            "&collection_id=" +
            encodeURIComponent(this.article.collection_id) +
            "&prix_unitaire=" +
            encodeURIComponent(this.article.prix_unitaire) +
            "&est_en_promotion=" +
            encodeURIComponent(this.article.est_en_promotion) +
            "&prix_barre=" +
            encodeURIComponent(this.article.prix_barre) +
            "&est_disponible=" +
            encodeURIComponent(this.article.est_disponible) +
            "&est_nouveau=" +
            encodeURIComponent(this.article.est_nouveau) +
            "&description=" +
            encodeURIComponent(this.article.description), 
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        let profil_msg = "";
        if (this.ifile) {
          let rr = await this.doUpdateProfilImage(data.id_article);
          if (rr.status == "SUCCESS") {
            profil_msg = "La mise à jour de l'image a été effectuée avec succès";
          } else if (rr.status == false) {
            profil_msg =
              "La mise à jour de l'image a renvoyé un erreur : " + rr.status;
          }
        }
        this.article = this.default_article;

        if(profil_msg != '') {
          this.$snotify.info(profil_msg, {
            timeout: 1500,
            showProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            
          });
        }

        this.$snotify.success("L'article a été enregistré avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        });

        
        // this.$swal({
        //   type: "success",
        //   title: "Effectué",
        //   text: "Le article a été enregistré avec succès. " + profil_msg,
        //   //footer: '<a href>Why do I have this issue?</a>'
        // });


        this.deleteImage();
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doEditArticle() {
      if (!this.verifierArticle(this.article).status) return;

      this.loading_message = "Enregistrement des modifications  en cours";
      this.is_doing_action = true;
      let url =
        this.constants.api_url + "/articles/edit.php/";
      let response = null;

      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          credentials: "include",
          headers,
          body: "id_article=" + encodeURIComponent(this.$route.params.id_article) + 
          "&nom=" +
            encodeURIComponent(this.article.nom) +
            "&collection_id=" +
            encodeURIComponent(this.article.collection_id) +
            "&prix_unitaire=" +
            encodeURIComponent(this.article.prix_unitaire) +
            "&est_en_promotion=" +
            encodeURIComponent(this.article.est_en_promotion) +
            "&prix_barre=" +
            encodeURIComponent(this.article.prix_barre) +
            "&est_disponible=" +
            encodeURIComponent(this.article.est_disponible) +
            "&est_nouveau=" +
            encodeURIComponent(this.article.est_nouveau) +
            "&description=" +
            encodeURIComponent(this.article.description),
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        let profil_msg = "";
        if (this.ifile) {
          let rr = await this.doUpdateProfilImage(
            this.$route.params.id_article
          );
          if (rr.status == "SUCCESS") {
            profil_msg = "La mise à jour du profile a été effectué avec succès";
          } else if (rr.status == false) {
            profil_msg =
              "La mise à jour de l'image a renvoyé un erreur : " + rr.status;
          }
        }
        this.article = this.default_article;
        this.deleteImage();
        
        if(profil_msg != '')  {
          this.$snotify.info(profil_msg, {
            timeout: 1500,
            showProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            
          });
        }

        this.$snotify.success("L'article a été modifé avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        });

        

        // await this.$swal({
        //   type: "success",
        //   title: "Effectué",
        //   text: "Le article a été éditée avec succès. " + profil_msg,
        //   //footer: '<a href>Why do I have this issue?</a>'
        // });
        this.$router.push("/admin/articles");
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doUpdateProfilImage(id_article) {
      if (this.profile_image_changed && this.ifile) {
        this.loading_message = "Mise à jour de l'image en cours";
        let url =
          this.constants.api_url + "/articles/change-image.php?id_article=" + id_article;

        const formData = new FormData();
        formData.append("img", this.ifile, this.ifile.name);

        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        let response = await fetch(url, {
          method: "post",
          body: formData,
          credentials: "include",
          cache: "no-cache",
          // headers,
        });

        let data = await response.json();
        if (data.status == "SUCCESS") {
          return {
            status: "SUCCESS",
          };
        } else {
          return data;
        }
      } else {
        return { status: "NO_CHANGE" };
      }
    },
    onProfilImageChange(event) {
      let file = event.target.files[0];
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      if (!file) return;
      let reader = new FileReader();
      reader.onload = () => {
        this.img_profil = reader.result;
        let base64 = reader.result.substr(
          reader.result.indexOf(",") + 1,
          reader.result.length - (reader.result.indexOf(",") + 1)
        );
        this.filename = file.name;
        this.ifile = file;
        this.preview_img = this.img_profil;
        this.preview_img_base64 = base64;

        this.profile_image_changed = true;
        // setImagesChanged(true);
      };
      reader.readAsDataURL(file);
    },
    deleteImage() {
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      this.profile_image_changed = true;
    },
    getImgObjModel(
      id_article,
      nom_fichier = "",
      id_image = -1,
      description = ""
    ) {
      return {
        description,
        id_article: id_article,
        code_contexte: "IMG_JOUEUR",
        nom_fichier,
        id_image,
      };
    },
  },
  async created() {
    if (!this.add_mode) {
     this.nbr_total_chargements += 1;
      this.is_loading_articles = true;
      this.loadArticle().then(() => this.verifierChargement()); 
    }
    this.loadCollections().then(() => this.verifierChargement()); 
  },
  data() {
    return {
      constants,

      nbr_total_chargements: 1,
      nbr_charges: 0,

      sortBy: " ",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      mouvements_article: [],
      fields: [
        { key: "date_mouvement", sortable: true },
        { key: "etat_article", sortable: false },
        { key: "observation", sortable: true },
        // { key: "action", sortable: true },
      ],

      is_loading_articles: false,
      is_doing_action: false,
      listCollections : [],
      article: { 
        id_article: -1,
        nom : "",
        description : "",
        prix_unitaire : 0,
        est_en_promotion : false,
        prix_barre : 0,
        est_disponible : true,
        collection_id : -1,
        est_nouveau : true
      },
      default_article: { 
        id_article: -1,
        nom : "",
        description : "",
        prix_unitaire : 0,
        est_en_promotion : false,
        prix_barre : 0,
        est_disponible : true,
        collection_id : -1,
        est_nouveau : true
      },

      loading_message: "Chargement de données en cours",

      // sur la preview de l'image de profil
      filename: "",
      preview_img: null,
      preview_img_base64: "",
      ifile: null,
      img_profil: null,
      profile_image_changed: false,
    };
  },
  components: { 
  },
};
</script>

<template>
  <section class="news-grid mt-5">
    <div class="page-header">
      <h3 class="page-title">
        {{
          add_mode ? "Nouvelle Article" : "Editer les informations sur une Article"
        }}
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link :to="{ name: 'dashboard' }"
              >Administration</router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{
              add_mode
                ? "Nouvelle Article"
                : "Editer les informations sur un Article"
            }}
          </li>
        </ol>
      </nav>
    </div>

    <div class="mt-5 pt-4" v-if="is_loading_articles">
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement de données en cours</p>
        </div>
      </div>
    </div>
    <div class="row mt-5" v-else-if="!is_loading_articles">
      <div class="col-md-12">
        <div class="card mb-3" v-if="is_doing_action">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ loading_message }}</p>
          </div>
        </div>

        <div class="card mb-4">
          <div class="card-body">   
            <div class="row">
            <div class="col-md-7">
                <fieldset class="mt-4" :disabled="is_doing_action">
                <b-form-group label="Nom de l'article :" label-for="nom">
                    <b-form-input
                    type="text"
                    class="mb-3"
                    id="nom"
                    v-model="article.nom"
                    placeholder="Nom de l'article"
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Collection :" label-for="collection_article">
                    <select id="collection_article" v-model="article.collection_id" class="form-control">
                        <option :value="-1">-- Aucune collection --</option>
                        <option v-for="collection in listCollections" :key="collection.id_collection" :value="collection.id_collection">{{ collection.libelle }}</option>
                    </select>
                </b-form-group>

                <b-form-group label="Prix unitaire (en $) :" label-for="prix_unitaire">
                    <b-form-input
                    type="number"
                    class="mb-3"
                    id="prix_unitaire"
                    v-model="article.prix_unitaire"
                    placeholder="Prix unitaire"
                    ></b-form-input>
                </b-form-group>
    
                <b-form-group label="L'article est en promotion :" label-for="disponibilite_article">
                    <select v-model="article.est_en_promotion" class="form-control">
                        <option :value="1">Oui</option>
                        <option :value="0">Non</option>
                    </select>
                </b-form-group>

                <b-form-group label="Prix à afficher barré (en $) :" label-for="prix_barre">
                    <b-form-input
                    type="number"
                    class="mb-3"
                    id="prix_barre"
                    v-model="article.prix_barre"
                    placeholder="Prix unitaire"
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Disponibilité de l'Article :" label-for="disponibilite_article">
                    <select id="disponibilite_article" v-model="article.est_disponible" class="form-control">
                        <option :value="'1'">Disponible</option>
                        <option :value="'0'">Indisponible</option>
                    </select>
                </b-form-group>


                <b-form-group label="L'article est un nouvel article :" label-for="nouvel_article">
                    <select id="nouvel_article" v-model="article.est_nouveau" class="form-control">
                        <option :value="'1'">Oui</option>
                        <option :value="'0'">Non</option>
                    </select>
                </b-form-group>
 

                <b-form-group
                    label="Description :"
                    label-for="description"
                    class="mt-3"
                >
                    <b-form-textarea
                    id="description"
                    placeholder="Description"
                    :rows="15"
                    :max-rows="15"
                    v-model="article.description"
                    ></b-form-textarea>
                </b-form-group>
 

                <b-button
                    type="submit"
                    :disabled="is_doing_action"
                    variant="success"
                    class="mr-2 mt-4"
                    @click="add_mode ? doAddArticle() : doEditArticle()"
                    >{{
                    add_mode
                        ? "Enregistrer l'article"
                        : "Enregistrer les modifications apportées"
                    }}</b-button
                >
                </fieldset>
            </div>

            <div class="col-md-5">
                <!-- <b-form-input 
                accept="image/*"
                maxLength="2"
                onChange="onProfilImageChange"
                type="file"
                name="example-text-input"
                placeholder="Numéro
                du titre de paiement"
                ></b-form-input> -->
                <h6>Image de l'article</h6>
                <hr />
                <b-form-file
                id="inpu8"
                accept="image/*"
                maxLength="2"
                placeholder="Sélectionner un fichier"
                @change="onProfilImageChange"
                ></b-form-file>

                <div
                class="d-flex flex-column align-items-center mt-5"
                v-if="
                    ifile != null || (!add_mode && !profile_image_changed)
                "
                >
                <img
                    :src="
                    constants.api_url +
                    '/images/articles/' +
                    article.id_article +
                    '.jpg'
                    "
                    alt="Profil IMage"
                    style="max-width: 90%"
                    class=" border mb-3"
                    v-if="!add_mode && !profile_image_changed"
                />
                <img
                    :src="img_profil"
                    alt="profile image"
                    style="max-width: 90%"
                    class=" mb-3 border"
                    v-else
                />
                <button
                    type="button"
                    class="btn btn-sm btn-outline-danger"
                    @click="deleteImage"
                >
                    Supprimer
                </button>
                </div>
            </div>
            </div>  
          </div>
        </div>
      </div>
    </div>
 
  </section>
</template>
<style scoped>
.detail-article-item {
  font-size: 1.08em;
}
.biographie-text {
  font-size: 1.1em;
}
</style>